import React from 'react';
import styles from '../style';
import { stats } from '../constants';
import Counter from '../common/Counter';

const Stat = () => {
  return (
    <div className={`${styles.flexCenter} flex-row flex-wrap sm:mb-20 mb-6`}>
      {stats.map(stat =>{
        return (
          <div key={stat.id} className={`flex-1 flex justify-center items-center flex-row m-3`}>
            <h4 className='font-poppins font-semibold xs:text-[40px] text-[30px] xs:leading-[53px] xs:leading-[53px] leading-[43px] text-white'>
            <Counter
              from={0}
              to={stat.value}
              textBefore={stat.textBefore ? stat.textBefore : ""}
              textAfter={stat.text}
            />
            </h4>
            <p className='font-poppins font-normal xs:text-[20px] text-[15px] xs:leading-[53px] xs:leading-[26px] leading-[21px] text-gradient uppercase ml-3'>{stat.title}</p>
          </div>
        )
      })}
    </div>
  )
}

export default Stat
